import { connect } from "react-redux";
import App from "../../component/AppComponent";
import { loadGeoCache, updateGeoCache, likeGeoCache } from "../../actions/geoCacheAction";
import { loggedInUser, registerUser } from "../../actions/authAction";

const mapStateToProps = state => ({
  geoCache: state.GeoCacheReducer
});

const mapDispatchToProps = dispatch => ({
  loadGeoCache: () => dispatch(loadGeoCache()),
  loggedInUser: () => dispatch(loggedInUser()),
  registerUser: () => dispatch(registerUser()),
  updateGeoCache: geoCache => dispatch(updateGeoCache(geoCache)),
  likeGeoCache: id => dispatch(likeGeoCache(id))
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(App);
