import { AUTH_USER, AUTH_ERROR, REG_ERROR, CLEAR_ERROR } from "./ActionTypes";
import { loginUserService, registerUserService } from "../service/AuthService";
import { setLocalToken } from "../helper/auth";

export function loadLoginSuccess(loginData) {
  return {
    type: AUTH_USER,
    loginData
  };
}

export function loginFailure(errMsg) {
  return {
    type: AUTH_ERROR,
    errMsg
  };
}

export function loadRegisterSuccess(loginData) {
  return {
    type: AUTH_USER,
    loginData
  };
}

export function resgisterFailure(errMsg) {
  return {
    type: REG_ERROR,
    errMsg
  };
}

export function clearError() {
  return {
    type: CLEAR_ERROR,
  };
}

export function loggedInUser(data) {
  return function (dispatch) {
    const user = {
      email: data.email,
      password: data.password
    };
    return loginUserService(user)
      .then(loginData => {
        setLocalToken(loginData);
        dispatch(loadLoginSuccess(loginData));
        data.redirect();
      })
      .catch(error => {
        dispatch(loginFailure("Invalid Credentials"));
      });
  };
}

export function registerUser(user) {
  return function (dispatch) {
    return registerUserService(user)
      .then(regUser => {
        dispatch(loadRegisterSuccess(regUser));
      })
      .catch(error => {
        dispatch(resgisterFailure({ regErr: "Error! Email exists or input details is incorrect" }));
      });
  };
}

export const logout = () => {
  localStorage.removeItem("authUser");

  return {
    type: AUTH_USER,
    data: ""
  };
};
