// import React from 'react'
import ReactDOM from "react-dom";
// import { Provider } from 'react-redux'
// import { configureStore, history } from './store/configureStore'
// import { ConnectedRouter } from 'react-router-redux';
import { Route, Switch } from "react-router-dom";
import React from "react";
import { ActionCableProvider } from "actioncable-client-react";
import { Provider } from "react-redux";
import { ConnectedRouter } from "connected-react-router";
import store, { history } from "./store";
import Auth from "./container/Auth";
import Login from "./container/LoginContainer";
import Register from "./container/RegisterContainer";
import "bootstrap/dist/css/bootstrap.min.css";

// Other Imports
import * as registerServiceWorker from "./serviceWorker";
import AppContainer from "./container/AppContainer";
const WSS_URL = `wss://blooming-cove-35281.herokuapp.com/cabel`;

ReactDOM.render(
  <Provider store={store}>
    <ConnectedRouter history={history}>
      <Switch>
        <Route excat path="/register" component={Register} />
        <Route excat path="/login" component={Login} />
        <Auth>
          <ActionCableProvider url={WSS_URL}>
            <Route excat path="/" component={AppContainer} />
          </ActionCableProvider>
        </Auth>
      </Switch>
    </ConnectedRouter>
  </Provider>,
  document.getElementById("root")
);
registerServiceWorker.register();
