import axios from "axios";
import { getToken } from '../helper/auth'

export function fetchGeoDetails() {
  const token = getToken()
  return axios
    .get(
      `https://blooming-cove-35281.herokuapp.com/api/geo_caches`,
      {
        headers: {
          "Access-Control-Allow-Origin": "*",
          Authorization: `Bearer ${token}`
        },
        crossdomain: true
      }
    )
    .then(response => response.data)
    .catch(err => {
      throw err;
    });
}


export function likeGeo(id) {
  const token = getToken()
  return axios
    .post(
      `https://blooming-cove-35281.herokuapp.com/api/geo_caches/${id}/like`, null,
      {
        headers: {
          "Access-Control-Allow-Origin": "*",
          Authorization: `Bearer ${token}`
        },
        crossdomain: true
      }
    )
    .then(response => response)
    .catch(err => {
      throw err;
    });
}
