import { AUTH_USER, AUTH_ERROR, REG_ERROR, CLEAR_ERROR } from "../actions/ActionTypes";
import initialState from "./initialState";

export default function authReducer(state = initialState, action) {
  switch (action.type) {
    case AUTH_USER:
      return { ...state, authenticated: action.loginData };
    case AUTH_ERROR:
      return { ...state, errorMessage: action.errMsg };
    case REG_ERROR:
      return { ...state, regErrorMessage: action.errMsg };
    case CLEAR_ERROR:
      return {
        ...state, regErrorMessage: '', errorMessage: ''
      }

    default:
      return state;
  }
}
