import React, { Component } from "react";
import { withScriptjs, withGoogleMap, GoogleMap, Marker, InfoWindow } from "react-google-maps"

class Map extends Component {

  state = {
    infoOpen: false,
    i: null
  }

  onCloseInfo = () => {
    this.setState({
      index: null
    })
  }

  infoOpen = i => {
    this.setState({
      index: i
    })
  }

  render() {
    const { geoCache, likeGeoCache } = this.props
    const { index } = this.state
    return (
      <GoogleMap
        defaultZoom={2}
        defaultCenter={{ lat: -34.397, lng: 150.644 }}
      >
        {
          geoCache.map((v, i) =>
            <Marker
              key={i}
              position={{ lat: Number(v.lat), lng: Number(v.lng) }}
              onClick={() => this.infoOpen(i)}
            >
              {index === i && <InfoWindow onCloseClick={this.onCloseInfo}>
                <p>
                  {`${v.lat}, ${v.lng}`}
                  <br />
                  <button onClick={() => likeGeoCache(v.id)}>Like</button>
                </p>
              </InfoWindow>}
            </Marker>
          )
        }
      </GoogleMap>
    );
  }
}

export default withScriptjs(withGoogleMap(Map));
