import { LOAD_GEO_CACHE, UPDATE_GEO_CACHE, LIKE_GEO_CACHE } from "./ActionTypes";
import { fetchGeoDetails, likeGeo } from "../service/Geocaches";

export function loadGeoCacheSuccess(geoCache) {
  return {
    type: LOAD_GEO_CACHE,
    geoCache
  };
}

export function updateGeoCache(geoCacheDetails) {
  return {
    type: UPDATE_GEO_CACHE,
    geoCacheDetails
  };
}

export function likeGeoCacheSuccess(id) {
  return {
    type: LIKE_GEO_CACHE,
    id
  };
}

export function likeGeoCache(id) {
  return function (dispatch) {
    return likeGeo(id)
      .then(() => {
        dispatch(likeGeoCacheSuccess(id));
      })
      .catch(error => {
        throw error;
      });
  };
}

export function loadGeoCache() {
  return function (dispatch) {
    return fetchGeoDetails()
      .then(geoCache => {
        dispatch(loadGeoCacheSuccess(geoCache));
      })
      .catch(error => {
        throw error;
      });
  };
}
