import { LOAD_GEO_CACHE, UPDATE_GEO_CACHE, LIKE_GEO_CACHE } from '../actions/ActionTypes';
import initialState from './initialState';

export default function GeoCacheReducer(state = initialState.geoCache, action) {
  switch (action.type) {
    case LOAD_GEO_CACHE:
      return action.geoCache;

    case UPDATE_GEO_CACHE:
      return [
        ...state.filter(geo => geo.id != action.geoCacheDetails.id),
        Object.assign({}, action.geoCacheDetails)
      ];

      case LIKE_GEO_CACHE:
      const newStae = {...state}
      return newStae.map(geo => {
        if(geo.id !== action.id) {
          return geo
        } else {
          return {
            ...geo,
            "likes": Number(geo.likes) + 1
          }
        }
      })

    default:
      return state;
  }
}
