import axios from "axios";

export function loginUserService(user) {
  let loginUser = {
    grant_type: "password",
    email: user.email,
    password: user.password
  };

  return axios
    .post(
      "https://blooming-cove-35281.herokuapp.com/api/oauth/token",
      loginUser,
      {
        crossdomain: true
      }
    )
    .then(response => response.data)
    .catch(err => {
      throw err;
    });
}

export function registerUserService(register) {
  let user = {
    user: {
      email: register.email,
      first_name: register.firstName,
      last_name: register.lastName,
      password: register.password,
      username: register.userName
    }
  };

  return axios
    .post("https://blooming-cove-35281.herokuapp.com/api/users", user, {
      crossdomain: true
    })
    .then(response => response.data)
    .catch(err => {
      throw err;
    });
}
