import React, { Component, Fragment } from "react";
import Header from "../Header";
import {
  Button,
  Form,
  FormGroup,
  Label,
  Input,
  FormText,
  Row,
  Col
} from "reactstrap";

class Register extends Component {
  state = {
    firstName: "",
    lastName: "",
    userName: "",
    email: "",
    password: "",
    submitted: false
  };

  componentWillUnmount() {
    const { clearError } = this.props;
    clearError();
  }

  handleChange = event => {
    this.setState({ [event.target.name]: event.target.value });
  };

  doRegister = () => {
    const { registerUser } = this.props;
    const { firstName, lastName, userName, email, password } = this.state;
    this.setState({ submitted: true });
    if (firstName && lastName && userName && email && password) {
      registerUser({ firstName, lastName, userName, email, password });
    }
  };

  render() {
    const {
      firstName,
      lastName,
      userName,
      email,
      password,
      submitted
    } = this.state;
    const { errorMessage } = this.props
    return (
      <Fragment>
        <Header authenticated={false} {...this.props} />
        <Row>
          <Col md={{ size: 6, offset: 3 }}>
            <div style={{ color: 'red' }}>{errorMessage ? errorMessage : null}</div>
            <Form>
              <Row form>
                <Col md={12}>
                  <FormGroup>
                    <Label>First Name</Label>
                    <Input
                      type="text"
                      name="firstName"
                      id="FirstName"
                      placeholder="Enter First Name"
                      onChange={this.handleChange}
                    />
                    {submitted && !firstName && (
                      <div className="help-block" style={{ color: 'red' }}>First Name is required</div>
                    )}
                  </FormGroup>
                  <FormGroup>
                    <Label>Last Name</Label>
                    <Input
                      type="text"
                      name="lastName"
                      id="LastName"
                      placeholder="Enter Last Name"
                      onChange={this.handleChange}
                    />
                    {submitted && !lastName && (
                      <div className="help-block" style={{ color: 'red' }}>Last Name is required</div>
                    )}
                  </FormGroup>

                  <FormGroup>
                    <Label>User Name</Label>
                    <Input
                      type="text"
                      name="userName"
                      id="UserName"
                      placeholder="Enter User Name"
                      onChange={this.handleChange}
                    />
                    {submitted && !userName && (
                      <div className="help-block" style={{ color: 'red' }}>User Name is required</div>
                    )}
                  </FormGroup>
                  <FormGroup>
                    <Label>Email</Label>
                    <Input
                      type="email"
                      name="email"
                      id="Email"
                      placeholder="Enter Email"
                      onChange={this.handleChange}
                    />
                    {submitted && !email && (
                      <div className="help-block" style={{ color: 'red' }}>Email is required</div>
                    )}
                  </FormGroup>
                  <FormGroup>
                    <Label>Password</Label>
                    <Input
                      type="password"
                      name="password"
                      id="Password"
                      placeholder="Enter Password"
                      onChange={this.handleChange}
                    />
                    {submitted && !password && (
                      <div className="help-block" style={{ color: 'red' }}>Password is required</div>
                    )}
                  </FormGroup>
                </Col>
              </Row>

              <Button onClick={this.doRegister}>Submit</Button>
            </Form>
          </Col>
        </Row>
      </Fragment>
    );
  }
}

export default Register;
